body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-manrope: Manrope;
  --font-phudu: Phudu;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-lgi: 19px;
  --font-size-45xl: 64px;
  --font-size-32xl: 51px;
  --font-size-19xl: 38px;
  --font-size-lg: 18px;
  --font-size-sm: 14px;
  --font-size-mini: 15px;

  /* Colors */
  --color-lightcyan: #d2eef9;
  --color-gray-100: #131313;
  --color-gray-200: #041415;
  --color-gray-300: rgba(19, 19, 19, 0.5);
  --color-gray-400: rgba(19, 19, 19, 0.3);
  --color-coral: #ff8360;
  --color-white: #fff;
  --color-darkslategray-100: #273436;
  --color-darkslategray-200: #094045;
  --color-darkslategray-300: rgba(39, 52, 54, 0.7);
  --color-dimgray-100: #596669;
  --color-dimgray-200: rgba(89, 102, 105, 0.09);
  --color-firebrick: #cc4f2e;
  --color-black: #000;

  /* Gaps */
  --gap-xl: 20px;
  --gap-21xl: 40px;
  --gap-lg: 18px;
  --gap-smi: 13px;
  --gap-13xl: 32px;
  --gap-base: 16px;
  --gap-smi-5: 12.5px;
  --gap-mid-5: 17.5px;
  --gap-5xl: 24px;
  --gap-mid: 17px;
  --gap-2xl: 21px;

  /* Paddings */
  --padding-xl: 20px;
  --padding-13xl: 32px;
  --padding-29xl: 48px;
  --padding-9xl: 28px;
  --padding-2xl: 21px;
  --padding-21xl: 40px;
  --padding-12xs: 1px;
  --padding-5xl: 24px;
  --padding-28xl: 47px;
  --padding-smi: 13px;
  --padding-xs: 12px;
  --padding-47xl: 66px;
  --padding-14xl: 33px;
  --padding-39xl: 58px;
  --padding-5xs: 8px;

  /* Border radiuses */
  --br-5xl: 24px;
  --br-base: 16px;
  --br-xl: 20px;
}
